<template>
  <el-dialog
    center
    custom-class="create-repair"
    title="创建报修单"
    :close-on-click-modal="false"
    :visible.sync="show"
    :modal="isModal"
    :modal-append-to-body="false"
    @close="close"
  >
    <el-form
      label-position="right"
      :model="Form"
      :rules="Rules"
      ref="Form"
      :label-width="LabelWidth"
      size="mini"
      status-icon
      @keyup.enter.native="ConfirmForm"
    >
      <el-row :gutter="5" v-for="(item, index) in formList" :key="index">
        <el-col v-for="(a, b) in item" :key="b" :span="a.span">
          <el-form-item :label="a.label + ': '" :prop="a.prop">
            <span v-if="a.type === 'text'">{{ Form[a.prop] }}</span>
            <Room
              :Level="3"
              :value.sync="Form[a.prop]"
              :name.sync="Form.deviceName"
              :ClsRoomID.sync="Form.ClsRoomID"
              :Values.sync="RoomID"
              placeholder="请选择教室"
              v-if="a.type === 'equip'"
            />
            <el-select v-model="Form[a.prop]" placeholder="请选择" v-if="a.type === 'sel'" @change="getTypename">
              <el-option v-for="(item, index) in FTypeArry" :key="index" :label="item.Value" :value="item.code"></el-option>
            </el-select>
            <el-input type="textarea" :rows="5" v-if="a.type === 'desc'" v-model="Form[a.prop]"></el-input>
            <Meta :value.sync="comFault" v-if="a.type === 'code'" code="FJRH_CommonFaults" @change="GetFault" />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="报修照片:" prop="pic">
            <Upload fileType=".jpg, .png" slotTip="只能上传jpg/png文件，且不超过500kb" ref="fp"></Upload>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer">
      <el-button-group>
        <el-button
          type="primary"
          icon="el-icon-check"
          size="mini"
          @click="ConfirmForm"
          autofocus
          native-type="submit"
          :loading="loading"
        >确 定</el-button>
        <el-button icon="el-icon-close" size="mini" @click="close">取 消</el-button>
      </el-button-group>
    </div>
  </el-dialog>
</template>
<script>
import api from "@/scripts/api";
import { InitSearch, RequestInfo } from "@/scripts/utils";
import store from "@/scripts/store";
let userId = "";
let uname = "";
export default {
  components: {
    Room: () => import("@/components/CascaderTree"),
    Upload: () => import("@/components/FileUpload"),
    Meta: () => import("@/components/SelectMeta")
  },
  props: {
    LabelWidth: {
      type: String,
      default: "120px"
    },
    // 显示
    visible: {
      type: Boolean,
      default: false
    },
    // 设备编号
    Did: {
      type: String,
      default: ""
    },
    // 教室编号
    Cid: {
      type: String,
      default: ""
    },
    // 是否需要遮蔽层
    isModal: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      RoomID: [],
      show: false,
      FTypeArry: [],
      formList: [
        {
          label: "维修申报人",
          prop: "draapplicantname",
          type: "text",
          span: 24
        },
        {
          label: "报修教室",
          prop: "ClsRoomID",
          type: "equip",
          span: 12
        },
        {
          label: "故障类型",
          prop: "FTID",
          type: "sel",
          span: 12
        },
        {
          label: "常用故障",
          prop: "comFault",
          type: "code",
          span: 24
        },
        {
          label: "故障描述",
          prop: "DRFailure",
          type: "desc",
          span: 24
        }
      ],
      Form: {
        dojsrh: "", // 经手人号
        username: "", // 经手人
        dostatuscode: "", // 现状码
        ClsRoomID: "", //  教室ID
        clsroomname: "", // 教室名称
        deviceid: "", // 设备ID
        deviceName: "", // 设备名称
        draapplicant: "", // 报修申请人ID
        draapplicantname: "", // 报修申请人名称
        drapprover: "", // 最终审批人ID
        drapprovername: "", // 最终审批人名称
        FTID: "", // 故障类型编号
        ftypename: "", // 故障类型名称
        drfaulttime: "", // 故障发生时间
        DRFailure: "", // 故障现象
        picType: 20, // 图片类型
        pic: "", // 图片
        msg: "" // 消息
      },
      comFault: "",
      Rules: {
        DRFailure: [
          {
            required: true,
            trigger: "blur",
            message: "请填写故障描述"
          }
        ],
        ClsRoomID: [
          {
            required: true,
            trigger: "blur",
            message: "请选择教室"
          }
        ]
      },
      loading: false
    };
  },
  mounted () {
    this.formList = InitSearch(this.formList, 2, [
      { r: 1, c: 1 },
      { r: 3, c: 1 },
      { r: 4, c: 1 }
    ]);
    const udata = store.GetUserInfo();
    userId = udata.UserID;
    uname = udata.UserName;
    this.Form.draapplicant = userId;
    this.Form.draapplicantname = uname;
    this.Form.drapprover = userId;
    this.Form.drapprovername = uname;
    this.Form.dojsrh = userId;
    this.Form.username = uname;
    this.loadFtp();
  },
  watch: {
    visible (val) {
      this.show = val;
      // 重置this.Form
      if (!val) {
        this.$refs.Form.resetFields();
        this.Form.clsroomname = "";
        this.Form.ftypename = "";
        this.comFault = "";
      }
    },
    Did (val) {
      this.$nextTick(() => {
        this.Form.deviceid = val;
      });
    },
    Cid (val) {
      this.$nextTick(() => {
        this.Form.ClsRoomID = val;
      });
    }
  },
  methods: {
    async loadFtp () {
      const cdata = await api.FaultType.GetDatas();
      if (cdata.Datas && cdata.Datas.length > 0) {
        cdata.Datas.forEach(item => {
          item.code = item.Key.FTID;
        });
        this.FTypeArry = cdata.Datas;
      }
    },
    // 故障类型名称
    getTypename (e) {
      const s = this.FTypeArry.find(c => c.code === e);
      if (s) {
        this.Form.ftypename = s.Value;
      }
    },
    // 获取常用故障描述
    GetFault (val) {
      this.Form.DRFailure = val;
    },
    // 获取指定设备编号设备信息对应的房间信息
    async getRoom (id) {
      let clsrooms = "";
      if (id) {
        clsrooms = await api.Device.GetRoomData(id);
      }
      if (clsrooms) {
        // this._roomid = clsrooms.roomID;
        this.Form.dostatuscode = clsrooms.deviceState;
      } else this.Form.dostatuscode = "";
    },
    /**
     * 提交表单
     */
    async ConfirmForm () {
      this.loading = true;
      if (this.RoomID[2]) {
        this.Form.ClsRoomID = this.RoomID[2];
      }
      this.$refs.Form.validate(async valid => {
        if (valid) {
          this.getRoom(this.Form.deviceid);
          this.Form.pic = await this.$refs.fp.Upload(6, [
            { field: "picName", value: "" }
          ]);
          const data = [];
          for (var b in this.Form) {
            data.push(this.Form[b]);
          }
          const res = await api.Repair.Create(...data);
          if (res) {
            this.show = false;
            this.loading = false;
            this.$emit("load");
          }
          RequestInfo(res, '保存')
        } else {
          return false;
        }
      });
    },
    close () {
      this.$emit("update:visible", false);
      this.$emit("close");
    }
  }
};
</script>
<style lang="less">
.main .viewbody .create-repair {
  min-width: 800px;
  width: 800px;
  .el-dialog__body {
    max-height: 60vh;
    overflow: auto;
    padding: 10px 30px 10px 0px;
  }
}
.create-repair .el-form-item__content {
  top: 1px;
}
</style>
