<template>
  <el-transfer
    id="myTransfer"
    ref="transfer"
    v-loading="LoadingDatas"
    element-loading-text="数据正在加载中"
    :titles="['未添加', '已添加']"
    filterable
    target-order="push"
    :filter-placeholder="placeholder"
    v-model="curDatas"
    @change="Changed"
    :data="datas"
  >
    <template slot="left-footer">
      <el-input
        placeholder="请输入内容"
        v-model="filter"
        ref="txtFilter"
        clearable
        @input="FilterInput"
        @clear="DoFilter"
        class="el-transfer-panel__filter"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="DoFilter"
        ></el-button>
      </el-input>
      <el-pagination
        :current-page.sync="PIndex"
        :page-size.sync="PageSize"
        :total="TotalCounts"
        :pager-count="5"
        layout="total, prev, pager, next"
        @current-change="PageChanged"
      ></el-pagination>
    </template>
  </el-transfer>
</template>

<script>
import $ from "jquery";
export default {
  props: {
    placeholder: { type: String, default: "请输入查询关键字" },
    ALLDatas: {
      type: Array,
      default: () => []
    },
    SelectedDatas: {
      type: Array,
      default: () => []
    },
    LoadingDatas: {
      type: Boolean,
      default: false
    },
    // 当前第几页
    PageIndex: {
      type: Number,
      default: 1
    },
    PageSize: {
      type: Number,
      default: 50
    },
    // 总行数
    TotalCounts: {
      type: Number,
      default: 0
    },
    // 查询值
    QueryValue: {
      type: String,
      default: ""
    }
  },
  watch: {
    PageIndex(newVal, oldVal) {
      this.PIndex = newVal;
    },
    ALLDatas(newVal, oldVal) {
      this.datas = [];
      newVal.forEach(element => {
        const find = this.datas.find(d => d.id === element.id);
        if (!find) {
          this.datas.push(element);
        }
      });
      this.compareSelected(this.SelectedDatas);
      this.$emit("update:value", this.curDatas);
    },
    SelectedDatas(newVal, oldVal) {
      this.curDatas = [];
      this.compareSelected(newVal);
      this.$emit("update:value", this.curDatas);
    }
  },
  data() {
    return { PIndex: this.PageIndex, datas: [], curDatas: [], filter: "" };
  },
  mounted() {
    this.$nextTick(() => {
      $(this.$refs.transfer.$refs.leftPanel.$children[1].$el).replaceWith(
        $(this.$refs.txtFilter.$el)
      );
    });
  },
  methods: {
    PageChanged(val) {
      this.$emit("update:PageIndex", val);
      this.$emit("PageChanged");
    },
    // 变化时 同步更新 原始的选择数组
    Changed(val, dir, key) {
      if (dir === "right") {
        key.forEach(k => {
          const find = this.ALLDatas.find(d => d.id === k);
          if (find) {
            this.SelectedDatas.push(find);
          }
        });
      } else {
        key.forEach(k => {
          const find = this.SelectedDatas.findIndex(d => d.id === k);
          this.SelectedDatas.splice(find, 1);
        });
      }
      this.$emit("update:SelectedDatas", this.SelectedDatas);
      this.$emit("update:value", val);
    },
    // 重新比较已经选择的
    compareSelected(val) {
      this.curDatas = [];
      val.forEach(element => {
        const findS = this.ALLDatas.find(d => d.id === element.id);
        if (!findS) {
          this.ALLDatas.push(element);
        }
        if (this.curDatas.indexOf(element.id) < 0) {
          this.curDatas.push(element.id);
        }
      });
    },
    // 输入时同步给父组件查询值
    FilterInput(val) {
      this.$emit("update:QueryValue", val);
    },
    DoFilter() {
      this.$emit("DoFilter", this.filter);
    }
  }
};
</script>

<style lang="less">
.el-transfer-panel__filter.el-input {
  width: calc(100% - 30px);
}
</style>
