<template>
  <el-select filterable v-model="Value" :placeholder="Placeholder" @change="ChangeHandle">
    <el-option
      v-for="item in Datas"
      :key="item.NationalityCode"
      :label="`${item.NationalityShortName}（${item.NationalityCode}）`"
      :value="item.NationalityCode"
    ></el-option>
  </el-select>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    value: {
      type: String,
      default: ""
    },
    Placeholder: {
      type: String,
      default: "请选择"
    }
  },
  watch: {
    value(newVal, oldVal) {
      this.Value = this.value;
      if (newVal !== oldVal) this.LoadData();
    }
  },
  data() {
    return {
      Value: this.value,
      Datas: []
    };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle() {
      this.$emit("update:value", this.Value);
    },
    async LoadData() {
      const res = await api.Nat.GetDatas("", 1, 65535);
      if (res) {
        this.Datas = res.Datas;
      }
    }
  }
};
</script>

