<template>
  <el-select
    popper-class="selLesson"
    filterable
    v-model="Value"
    :placeholder="placeholder"
    :multiple="multiple"
    :collapse-tags="true"
    :clearable="false"
    @change="ChangeHandle"
    :size="size"
  >
    <el-option-group v-for="group in Datas" :key="group.label" :label="group.label">
      <el-option
        v-for="item in group.options"
        :key="item.value"
        :label="`${item.label} ${item.text}`"
        :value="item.value"
        :disabled="item.disabled"
      >
        <span>{{ item.label }}</span>
        <span>{{ item.text }}</span>
      </el-option>
    </el-option-group>
  </el-select>
</template>

<script>
import api from "@/scripts/api";
import moment from "moment";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    /**
     * 如果传入的日期是今日的话 则根据日期来禁用项
     */
    DisabledByDate: {},
    value: {},
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: "请选择"
    }
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.Value = this.value;
        // this.LoadData();
      }
    },
    DisabledByDate(newVal, oldVal) {
      this.Datas.forEach(g => {
        const afterday = newVal
          ? moment(newVal).isAfter(moment(new Date()), "day")
          : true;
        g.options.forEach(d => {
          d.disabled =
            !afterday &&
            moment(d.LessonClsTime, "HH:mm:ss").isSameOrBefore(new Date());

          if (d.disabled && d.value === this.Value) {
            this.Value = "";
            this.$emit("update:value", this.Value);
          }
        });
      });
    }
  },
  data() {
    return {
      Value: this.value,
      Datas: []
    };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle() {
      this.$emit("update:value", this.Value);
      this.Datas.forEach(group => {
        group.options.forEach(item => {
          if (item.value === this.Value) {
            this.$emit("update:LessonClsTime", item.LessonClsTime);
            this.$emit("update:LessonOutTime", item.LessonOutTime);
          }
        });
      });
      this.$emit("Filter", this.Value);
    },
    async LoadData() {
      this.Datas = [
        {
          label: "上午",
          options: []
        },
        {
          label: "下午",
          options: []
        }
      ];
      const res = await api.Lesson.GetCurrentLessions();
      if (res) {
        const afterday = this.DisabledByDate
          ? moment(this.DisabledByDate).isAfter(moment(new Date()), "day")
          : true;
        if (res.Result) {
          res.Datas.forEach(element => {
            let ampm = 0;
            if (element.LessonClsTime < "12:00:00") {
              ampm = 0;
            } else {
              ampm = 1;
            }

            this.Datas[ampm].options.push({
              label: "第" + element.LessonSession + "节",
              value: element.LessonSession,
              disabled:
                !afterday &&
                moment(element.LessonClsTime, "HH:mm:ss").isSameOrBefore(
                  new Date()
                ),
              LessonClsTime: element.LessonClsTime,
              LessonOutTime: element.LessonOutTime,
              text:
                element.LessonClsTime.substring(0, 5) +
                "~" +
                element.LessonOutTime.substring(0, 5)
            });
          });
          this.Value = this.value;
        }
      }
    }
  }
};
</script>

<style lang="less">
.selLesson {
  .el-select-group__wrap {
    .el-select-group {
      .el-select-dropdown__item {
        span {
          float: left;
        }

        span:first-child {
          width: 42px;
          margin-right: 3px;
        }

        span:last-child::before {
          content: "(";
        }

        span:last-child::after {
          content: ")";
          margin-right: 20px;
        }
      }
    }
  }
}
</style>