<template>
  <div class="video-player">
    <div class="Player" ref="videoBox">
      <Player
        :IsLive.sync="IsLive"
        ref="Player"
        :autoplay="true"
        :source="CurrentURL"
        :type="Type"
        :IsMuted="false"
        :IsPaused.sync="IsPaused"
        :duration.sync="Duration"
        @CurrentTime="CurrentTimeChange"
        @PlayEnded="PlayEnded"
        @dblclick="FullScreenHandler"
      />
      <div class="controlbar">
        <div class="controlbar-bg"></div>
        <el-slider
          v-if="!IsLive"
          class="progress"
          v-model="progress"
          :min="0"
          :max="Duration"
          @change="SetProcess"
          @mousedown.native="progressMousedownHandler"
          @mouseup.native="progressMouseupHandler"
          :format-tooltip="FormatDuration"
        ></el-slider>

        <span>{{IsPaused}}</span>
        <el-tooltip content="播放" placement="top">
          <div class="play-btn iconfont icon_Play" v-if="IsPaused " @click="Play"></div>
        </el-tooltip>
        <el-tooltip content="暂停" placement="top">
          <div class="play-btn iconfont icon_pause_1" v-if="!IsPaused " @click="Pause"></div>
        </el-tooltip>
        <div class="time-display" v-if="!IsLive">
          <span class="current-time" style="display: inline;">{{FormatDuration(progress)}}</span>
          <span class="time-bound" style="display: inline;">/</span>
          <span class="duration" style="display: inline;">{{FormatDuration(Duration)}}</span>
        </div>
        <el-tooltip :content="!isFullScreen ? '全屏':'退出全屏'" placement="top">
          <div
            class="fullscreen-btn iconfont"
            @click="FullScreenHandler"
            :class="{'icon_fullscreen':!isFullScreen,'icon_exit_fullscreen':isFullScreen}"
          ></div>
        </el-tooltip>
        <el-popover popper-class="volume-control" trigger="hover" placement="top">
          <el-slider v-model="volume" vertical @change="SetVolume"></el-slider>
          <div
            slot="reference"
            class="volume iconfont"
            :class="{'icon_volume':volume > 0,'icon_mute':volume <=0}"
            @click="ChangeMuted"
          ></div>
        </el-popover>
      </div>
    </div>
  </div>
</template>
<script>

import screenfull from "screenfull";
import moment from "moment";
import { PrefixIntrger } from "@/scripts/utils";
// import store from "@/scripts/store";

export default {
  name: "r-video",
  components: {
    Player: () => import("@/components/Player/VPPlayer.vue")
  },
  props: {
    mediaSrc: {
      type: String,
      required: true
    },
    mediaPoster: {
      type: String,
      default: require("@/assets/img/live_lesson_poster.png")
    },
    mediaHeight: {
      type: String,
      default: "100%"
    },
    IsLive: {
      type: Boolean,
      default: false
    }
    // Videos: {
    //     type: Array,
    //     default: () => []
    //   },
    //   ShowPlayList: {
    //     type: Boolean,
    //     default: true
    //   },
    //   ShowThumb: {
    //     type: Boolean,
    //     default: false
    //   },
    //   SelectAble: {
    //     type: Boolean,
    //     default: true
    //   },
    //   CanExpand: {
    //     type: Boolean,
    //     default: false
    // }
  },
  data () {
    return {
      mediaLoading: true,
      // 暂停状态触摸
      mediaError: false,

      PlayList: [],
      CurrentURL: "http://",
      CurrentIndex: 0,
      Type: !this.IsLive ? "mp4" : "flv",
      // VODURL_Prefix: "",
      // VODURL_Suffix: "",
      IsLoaded: false,
      progress: 0,
      volume: 100,
      beforeMuted: 100,
      IsPaused: false,
      Duration: 0,
      ProgressIsDrag: false,
      IsExpand: false,
      isFullScreen: false // 是否全屏
    };
  },
  watch: {
    mediaSrc (newVal, oldVal) {
      this.CurrentURL = newVal;
      // if (newVal == null || newVal.length <= 0) {
      //   // this.Dispose();
      // } else {
      //   this.CurrentURL = `${this.VODURL_Prefix}${
      //     this.Videos[0].MaterialFileDirectory ? "/record" : "/vod"
      //   }${this.Videos[0].MaterialStorePath}${this.VODURL_Suffix}`;
      // }
    }
  },
  methods: {
    // 点击重新加载
    retry () {
      // this.mediaError = false;
      // setTimeout(() => {
      //   this.$refs["video"].load();
      // }, 200);
    },
    Download (row) {
      // const ConfigInfo = store.GetConfigInfo();
      // const FileSaver = require("file-saver");
      // FileSaver.saveAs(
      //   `http://${ConfigInfo.Host}:${ConfigInfo.Port}/FJRH.Campus/MaterialService/Download?mid=${row.MaterialID}`
      // );
    },
    PlayEnded () {
      this.$emit("PlayEnded");
    },
    // ChangePlay (item, index) {
    //   this.CurrentIndex = index;
    //   if (this.IsLive) {
    //     this.CurrentURL = `${item.Path}`;
    //   } else {
    //     this.CurrentURL = `${this.VODURL_Prefix}/vod${item.Path}${this.VODURL_Suffix}`;
    //   }
    //  // console.log(this.CurrentURL);
    // },
    // 全屏
    FullScreenHandler () {
      if (screenfull.isEnabled) {
        screenfull.toggle(this.$refs.videoBox);
      }
    },
    // 切换静音
    ChangeMuted () {
      if (this.volume > 0) {
        this.beforeMuted = this.volume;
        this.volume = 0;
      } else {
        this.volume = this.beforeMuted;
      }

      this.SetVolume(this.volume);
    },
    // 控制音量
    SetVolume (val) {
      if (this.$refs.Player) {
        this.$refs.Player.Volume(val);
      }
    },
    // 格式化时长
    FormatDuration (val) {
      const duration = moment.duration(val, "seconds");
      return `${PrefixIntrger(duration.hours(), 2)}:${PrefixIntrger(
        duration.minutes(),
        2
      )}:${PrefixIntrger(duration.seconds(), 2)}`;
    },
    progressMousedownHandler () {
      this.ProgressIsDrag = true;
    },
    progressMouseupHandler () {
      this.ProgressIsDrag = false;
    },
    // 更新进度
    CurrentTimeChange (val) {
      if (!this.ProgressIsDrag) this.progress = val;
    },
    // 控制进度
    SetProcess (val) {
      if (this.$refs.Player) {
        this.$refs.Player.CurrentTime(val);
      }
    },
    // 播放
    Play () {
      this.CurrentURL = this.mediaSrc;
      if (this.$refs.Player) {
        this.$refs.Player.Play();
      }
    },
    // 暂停
    Pause () {
      if (this.$refs.Player) {
        this.$refs.Player.Pause();
      }
    },
    // 停止
    Stop () {
      if (this.$refs.Player) {
        this.$refs.Player.Stop();
      }
    },
    // 释放
    Dispose () {
      try {
        if (this.$refs.Player) {
          console.log("释放");
          this.$refs.Player.Pause();
          this.$refs.Player.Dispose();
        }
      } catch (ex) {
        console.log(ex);
      }
      this.progress = 0;
      this.Duration = 0;
    }
  },
  mounted () {
    // setTimeout(() => {
    //   this.retry();
    // }, 20);

    screenfull.on("change", () => {
      this.isFullScreen = screenfull.isFullscreen;
    });
    // this.VODURL_Prefix = `http://${this.$root.SrvConfigInfo.VODHost}:${this.$root.SrvConfigInfo.VODPort}`;
    // this.LoadPlayList();
  },
  beforeDestroy () {
    screenfull.off("change");
    this.Dispose();
  }
};
</script>
