<template>
  <div class="sckviewer">
    <Calendar
      @MonthChanged="MonthChanged"
      @Changed="DateChanged"
      :ScheduledDate.sync="ScheduleDate"
      :ChooseDate.sync="ChooseDate"
    />
    <p class="title">{{FormatDate(ChooseDate)}}督导计划</p>
    <div class="lists" v-if="evalplans">
      <ul>
        <li v-for="(item,index) in evalplans" :key="index">
          <div :class="{'over':item.EVALPState !== 1}">
            <span>{{item.LessonClsTime}}</span>
            <span>~</span>
            <span>{{item.LessonOutTime}}</span>
          </div>
          <div>
            <p>
              <span>计划：{{item.EVALPName}}</span>
              <span>第{{item.CSLesson}}节</span>
            </p>
            <p>
              <span>课程：{{item.CSTitle}}</span>
              <span v-if="item.EVALPState === 0" class="red">已结束</span>
              <span v-if="item.EVALPState === 1" class="green">进行中</span>
              <span v-if="item.EVALPState === 2" class="blue">未开始</span>
            </p>
            <p>
              <span>教师：{{item.teachers}}</span>
            </p>
            <p>
              <span>班级：{{item.classs}}</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div v-if="!evalplans" class="none">
      <p>{{FormatMonthDay(ChooseDate)}} 无计划</p>
    </div>
  </div>
</template>

<script>
import api from "@/scripts/api";
import moment from "moment";
export default {
  components: {
    Calendar: () => import("@/components/Calendar")
  },
  props: {},
  data() {
    return {
      ScheduleDate: [],
      ChooseDate: new Date(),
      evalplans: []
    };
  },
  mounted() {
    var sDate = this.MonthHeadDate(this.ChooseDate);
    var eDate = this.MonthEndDate(this.ChooseDate);
    var sLogDate = this.DayHeadDate(this.ChooseDate);
    var eLogDate = this.DayEndDate(this.ChooseDate);
    this.LoadData(sDate, eDate, sLogDate, eLogDate);
  },
  methods: {
    FormatDate(day) {
      return moment(day).format("YYYY-MM-DD");
    },
    FormatMonthDay(day) {
      return moment(day).format("MM-DD");
    },
    MonthHeadDate(date) {
      return moment(date).format("YYYY-MM") + "-01 00:00:00";
    },
    MonthEndDate(date) {
      return (
        moment(date).format("Y-MM") +
        "-" +
        moment(moment(date).format("Y-MM"), "Y-MM").daysInMonth() +
        " 23:59:59"
      );
    },
    DayHeadDate(date) {
      return moment(date).format("YYYY-MM-DD") + " 00:00:00";
    },
    DayEndDate(date) {
      return moment(date).format("YYYY-MM-DD") + " 23:59:59";
    },
    DateChanged(date) {
      var sDate = this.MonthHeadDate(date);
      var eDate = this.MonthEndDate(date);
      var sLogDate = this.DayHeadDate(date);
      var eLogDate = this.DayEndDate(date);
      this.LoadData(sDate, eDate, sLogDate, eLogDate);
    },
    async LoadData(sDate, eDate, sLogDate, eLogDate) {
      const roomid = this.$parent.$route.params.id;
      const res = await api.EVALPlan.GetEVALByDate(
        roomid,
        sDate,
        eDate,
        sLogDate,
        eLogDate
      );
      this.ScheduleDate = [];
      this.evalplans = null;
      if (res.Result) {
        if (res.Dates && res.Dates.length > 0) {
          res.Dates.forEach(element => {
            this.ScheduleDate.push(element.Time);
          });
        }
        if (res.Datas && res.Datas.length > 0) {
          this.evalplans = [];

          res.Datas.forEach(element => {
            this.evalplans.push(element);
          });
        }
      } else {
        this.$message({
          showClose: true,
          message: res.Info,
          type: "error",
          duration: 1000
        });
      }
    },
  }
};
</script>

<style lang="less">
.sckviewer {
  .none {
    > p {
      font-size: 48px;
      text-align: center;
      line-height: 300px;
      color: #c7cdd3;
    }
  }
}
</style>