<template>
  <div class="topbar" :class="{ topmost: IsClient }">
    <div class="left">
      <el-button
        class="btn_menu"
        icon="iconfont icon_Menu"
        @click="OpenMenu"
        v-intro="'主菜单，所有的功能都可以从这里找到。'"
      ></el-button>
      <router-link
        to="/"
        class="logo"
        title="回首页"
        v-intro="'Logo，点击这里可以返回主页面。'"
      >
        <el-image :src="LOGOURL" fit="scale-down">
          <template slot="error">
            <i class="iconfont icon_xiaoyuan" />
            <span>{{ this.$root.PlatformName }}</span>
          </template>
        </el-image>
      </router-link>
    </div>
    <div></div>
    <div class="right">
      <el-popover
        trigger="hover"
        popper-class="popover_topbar popover_msg"
        width="320"
      >
        <header>站内消息通知</header>
        <div>
          <router-link to="#" v-for="Msg in Messages" :key="Msg.ID">
            <span>{{ Msg.Title }}</span>
            <time>{{ Msg.Time }}</time>
          </router-link>
        </div>
        <footer>
          <router-link to="/msg/0">查看更多</router-link>
        </footer>
        <el-button
          slot="reference"
          v-intro="'站内信：您收到的所有消息都在这里。'"
        >
          <el-badge :is-dot="UnReadMsg">
            <i class="iconfont icon_msg" />
          </el-badge>
        </el-button>
      </el-popover>
      <el-popover
        trigger="hover"
        popper-class="popover_topbar popover_acc"
        width="300"
      >
        <div class="acc">
          <div class="user">
            <el-avatar class="userPic" size="small">
              <el-image class="img" :src="DefaultHeadURL" @load="succ">
                <img :src="HeaderURL" slot="error" />
                <img :src="HeaderURL" slot="placeholder" />
              </el-image>
            </el-avatar>
            <span>{{ UserName }}</span>
          </div>
          <div class="oper">
            <router-link to="/ps/mi">基本资料</router-link>
            <router-link to="/ps/p">修改头像</router-link>
            <router-link to="/ps/s">个人设置</router-link>
          </div>
        </div>
        <footer>
          <a @click="LogOut">退出登录</a>
        </footer>
        <el-button
          slot="reference"
          v-intro="'您的头像，点击这里，您可以设置个人资料，修改头像等操作。'"
        >
          <el-avatar class="userPic" size="small">
            <el-image class="img" :src="DefaultHeadURL">
              <img :src="HeaderURL" slot="error" />
              <img :src="HeaderURL" slot="placeholder" />
            </el-image>
          </el-avatar>
        </el-button>
      </el-popover>
      <el-tooltip content="帮助引导">
        <el-button
          @click="Guide"
          icon="iconfont icon_Help"
          v-intro="'点击这里，将会对当前页面的功能进行一步一步引导。'"
        />
      </el-tooltip>
      <el-popover trigger="hover" popper-class="popover_topbar popover_Help">
        <div>
          <a @click="GoTo(h)" v-for="(h, index) in Helps" :key="index">
            {{ h.Title }}
          </a>
        </div>
        <footer>
          <router-link to="#">关于</router-link>
        </footer>
        <el-button icon="el-icon-warning-outline" slot="reference"></el-button>
      </el-popover>
      <el-tooltip content="传输列表" v-if="IsClient">
        <el-button @click="Transfer"
          ><el-badge
            class="badge"
            :value="TransferCount"
            :hidden="TransferCount <= 0"
            :max="99"
          >
            <i class="el-icon-sort" />
          </el-badge>
        </el-button>
      </el-tooltip>
      <el-tooltip content="最小化" v-if="IsClient">
        <el-button icon="el-icon-minus" @click="Minimize"></el-button>
      </el-tooltip>
      <el-tooltip content="关闭" v-if="IsClient">
        <el-button
          icon="el-icon-close"
          type="danger"
          @click="Close"
        ></el-button>
      </el-tooltip>
    </div>
  </div>
</template>

<script>
import store from "@/scripts/store";
import shell from "@/scripts/shell";

export default {
  data() {
    return {
      IsNeutral: false,
      Messages: [
        // { ID: "001", Title: "阿里云产品动态(1月刊)", Time: "2020-02-04 11:12" },
        // { ID: "001", Title: "阿里云产品动态(1月刊)", Time: "2020-02-04 11:12" },
        // { ID: "001", Title: "阿里云产品动态(1月刊)", Time: "2020-02-04 11:12" }
      ],
      Helps: [
        // { Code: "Guide", Title: "帮助引导" },
        { Title: "提交建议", URL: "#" },
        { Title: "技术支持", URL: "http://www.fjrh.cn", Open: true },
      ],
      DefaultHeadURL: null,
      HeaderURL: require("@/assets/img/icons/default_head.png"),
      UnReadMsg: false,
      UserName: "",
      LOGOURL: "",
      TransferCount: 0,
    };
  },
  mounted() {
    if (this.IsClient) {
      window.TransferCountCallBack = async (count) => {
        this.TransferCount = count;
      };
    }
    const userInfo = store.GetUserInfo();
    this.UserName = userInfo ? userInfo.UserName : "";
    if (userInfo) {
      this.DefaultHeadURL = `${this.$root.DownloadURL}?filename=${
        userInfo.UserID
      }&fileType=1&platFormType=1&${Math.random()}`;
    }
    this.$nextTick(() => {
      if (this.$root.SrvConfigInfo) {
        this.IsNeutral = this.$root.SrvConfigInfo.Neutral;
      }
      this.LOGOURL = `${
        this.$root.DownloadURL
      }?filename=${Date.now()}&fileType=11&platFormType=1`;
    });
  },
  methods: {
    succ() {
      var ImgObj = new Image();
      ImgObj.src = this.DefaultHeadURL;
      if (ImgObj.width === 60) {
        this.DefaultHeadURL = require("@/assets/img/icons/default_head.png");
      }
    },
    Guide() {
      // this.$intro().start();
      this.VueIntro.start();
      // this.VueIntro.showHints();
    },
    GoTo(helpItem) {
      if (helpItem.Code) {
        switch (helpItem.Code) {
          case "Guide": {
            break;
          }
        }
      } else {
        if (helpItem.URL) {
          if (window.Open) {
            if (!this.IsClient) window.open(helpItem.URL);
          } else {
            window.location = helpItem.URL;
          }
        }
      }
    },
    Minimize() {
      shell.Minimize();
    },
    Close() {
      store.RemoveToken();
      shell.Close();
    },
    Transfer() {
      shell.Transfer();
    },
    /**
     * 打开菜单
     */
    OpenMenu() {
      this.$parent.ShowMenu = !this.$parent.ShowMenu;
    },
    /**
     * 退出登录
     */
    LogOut() {
      store.RemoveToken();
      if (this.$root.SrvConfigInfo.UnifiedLogin === 2) {
        window.location.href = this.$root.SrvConfigInfo.UnifiedloginOutUrl;
        if (this.IsClient) {
          this.$router.push("/Login");
        } else {
          window.location.href = this.$root.SrvConfigInfo.UnifiedloginURL;
        }
      } else {
        this.$router.push("/Login");
      }
    },
    handleUrl(u) {
      this.DefaultHeadURL = u;
    },
  },
  created() {
    this.$bus.on("advatar", this.handleUrl);
  },
  beforeDestroy() {
    this.$bus.off("advatar", this.handleUrl);
  },
};
</script>

<style lang="less">
.topmost {
  z-index: 9999;
}
// .topbar.introjs-fixParent{
//    z-index: 999 !important;
// }
.topbar {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 999;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 1px 4px 0px;
  background-color: #ffffff;
  width: 100%;
  height: @TopBarHeight;
  display: grid;
  grid-template-columns: auto 1fr auto;

  > div {
    height: @TopBarHeight;
    display: flex;
    .el-button + .el-button {
      margin-left: 0px;
    }

    button {
      width: @TopBarHeight;
      height: @TopBarHeight;
      border: none;
      padding: 0px;
    }
  }

  .left {
    .btn_menu {
      background-color: @ThemeColor;

      i {
        font-size: 30px;
        color: #ffffff;
      }
    }

    .logo {
      height: @TopBarHeight;
      padding: 0px 12px;
      background: none;
      text-decoration: none;

      .el-image {
        width: auto;
        height: 40px;
        margin-top: 5px;

        .el-image__error,
        .el-image__inner,
        .el-image__placeholder {
          width: auto;
        }

        i,
        span {
          margin-top: -5px;
        }
      }

      * {
        float: left;
        line-height: @TopBarHeight;
      }

      span {
        font-size: 20px;
        font-weight: 600;
        margin-left: 4px;
      }

      i {
        font-size: 30px;
      }
    }

    .logo,
    .logo:link,
    .logo:visited,
    .logo:hover {
      color: @ThemeColor;
    }
  }

  .right {
    button {
      background-color: #fff;
      i {
        font-size: 20px;
      }
    }
    button:hover {
      i {
        color: @ThemeColor;
      }
    }
    .el-button--danger {
      color: #fff;
      background-color: #f56c6c;
      border-color: #f56c6c;
    }

    .el-button--danger:focus,
    .el-button--danger:hover {
      i {
        color: #fff;
      }
    }
  }
}

.popover_topbar[x-placement^="bottom"] {
  right: 15px;
  padding: 0px;
  font-size: 12px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px;
  transform: translate(0px, -15px);

  > div {
    > a {
      display: block;
      width: calc(100% - 12px * 2);
      color: rgb(51, 51, 51);
      padding: 0px 12px;
    }

    > a:hover {
      color: #111111;
      background-color: #f5f5f5;
    }
  }

  > footer {
    border-top: 1px solid #eeeeee;
    > a {
      text-align: left;
      color: #555555;
      display: block;
      width: 100%;
      text-overflow: ellipsis;
      white-space: nowrap;
      height: 36px;
      line-height: 34px;
      padding: 0px 12px;
    }
  }
}

.popover_msg[x-placement^="bottom"] {
  > header {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    background-color: #f5f5f5;
    line-height: 50px;
    padding: 0px 12px;
  }

  > div {
    > a {
      color: rgb(85, 85, 85);
      border-top: 1px solid #efefef;
      * {
        display: block;
      }
      > span {
        margin-bottom: 5px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      > time {
        display: block;
        color: rgb(136, 136, 136);
      }
    }

    > a:first-child {
      border-top: 0px;
    }
  }

  .popper__arrow::after {
    top: 1px;
    margin-left: -6px;
    border-top-width: 0;
    border-bottom-color: #f5f5f5;
  }
}

.popover_acc[x-placement^="bottom"] {
  .acc {
    padding: 12px;

    .user {
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      span:last-child {
        margin-left: 12px;
      }
    }

    .oper {
      display: flex;
      margin-top: 10px;

      > a {
        color: @FontColor;
        flex: 1 1 0%;
        padding: 0px 8px;
        border-left: 1px solid #dedede;
        text-align: center;
      }

      > a:first-child {
        border-left: none;
      }

      > a:hover {
        background-color: none !important;
        color: @ThemeColor;
      }
    }
  }

  > footer {
    background: none #f7f7f7;

    > a {
      color: @ThemeColor;
      text-align: center;
    }
  }
}

.popover_Help[x-placement^="bottom"] {
  min-width: 120px;

  > div {
    > a {
      color: @FontColor;
      height: 32px;
      line-height: 30px;
    }
  }
}

.el-avatar.userPic {
  .el-image {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
