<template>
  <div class="import-source">
    <div class="import-box">
      <div class="inner-box">
        <div class="top">
          <i class="iconfont icon_upload"></i>
          <p class="p">拖拽文件到此处也可以上传</p>
          <p class="tip" v-if="type==='video'">推荐使用mp4和flv格式</p>
          <p class="tip" v-if="type==='audio'">推荐使用mp3，WMA,FLAC,WAV格式</p>
          <p class="tip" v-if="type==='doc'">请上传doc，docx，pdf，ppt，pptx格式</p>
        </div>
        <el-button
          class="btn-upload"
          type="primary"
          icon="iconfont icon_upload"
          slot="trigger"
          size="mini"
        >上传文件</el-button>
      </div>
      <el-row class="tip-row" v-if="type==='video'">
        <el-col :span="8">视频大小：上传的文件大小上限600M，视频内容最大时长2hv</el-col>
        <el-col :span="8">上传格式：mp4,flv,avi,wmv,mov,webm,mpeg4,ts,mpg,rm,rmvb,mkv,m4v.</el-col>
        <el-col :span="8">视频码率：分别率最大支持1920×1080 120fps，音频码率最高320kbps（AAC编码）</el-col>
      </el-row>
      <el-row class="tip-row" v-if="type==='audio'">
        <el-col :span="8">视频大小：上传的文件大小上限200M</el-col>
        <el-col :span="8">上传格式：MP3，WMA，FLAC，WAV.</el-col>
        <el-col :span="8">视频码率：音频码率最高400kbps</el-col>
      </el-row>
    </div>
    <div class="title">上传文件列表</div>
    <SubTable
      custom-class
      :Loading="Loading"
      :Subbutton="false"
      :ShowSelectColumn="false"
      :ShowExport="false"
      :Columns="Columns"
      :Datas.sync="Datas"
      @LoadData="LoadData"
    >
      <template slot="ExAction" slot-scope="scope">
        <el-button
          type="primary"
          icon="el-icon-refresh-right"
          size="mini"
          plain
          @click="reUpload(scope.row)"
        >重新上传</el-button>
      </template>
    </SubTable>
    <div class="footer">
      <el-button type="primary" class="btn-file-cfm">完成</el-button>
      <span class="upload-tip">使用上传文件创建广播计划</span>
    </div>
  </div>
</template>

<script>
// import api from "@/scripts/api";
export default {
  components: {
    SubTable: () => import("@/components/SubTable")
  },
  props: {
    type: {
      type: String,
      default: "video"
    }
  },
  data() {
    return {
      Loading: false,
      Datas: [
        {
          fileName: "dfsdfsd"
        }
      ],
      Columns: [
        {
          Code: "fileName",
          Header: "文件名称",
          Fixed: "left",
          MinWidth: "150"
        },
        {
          Code: "curSpeed",
          Header: "当前速度",
          Slot: true,
          MinWidth: "100"
        },
        {
          Code: "uploaded",
          Header: "上传进度",
          Slot: true,
          MinWidth: "120"
        },
        {
          Code: "progress",
          Header: "进度条",
          MinWidth: "180"
        },
        {
          Code: "desc",
          Header: "操作",
          Slot: true,
          Fixed: "right",
          ShowBaseActionBtn: true,
          NeedViewBtn: false,
          UnNeedEditBtn: true,
          UnNeedEstateBtn: true,
          Align: "center",
          Width: "300"
        }
      ]
    };
  },
  computed: {
    acceptType() {
      switch (this.$route.params.type) {
        case "video":
          return "video/*";
        case "audio":
          return "audio/*";
        case "doc":
          return ".doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document";
        default:
          return "video/*";
      }
    }
  },
  methods: {
    LoadData() {},
    reUpload(row) {}
  }
};
</script>

<style lang="less">
.import-source {
  .import-box {
    width: 100%;
    height: 360px;
    margin-top: 10px;
    border-radius: 4px;
    border: 1px solid @ThemeColor;
    text-align: center;
    .inner-box {
      width: 600px;
      height: 260px;
      padding: 0 20px;
      margin: 40px auto 0;
      box-sizing: border-box;
      border: 1px dashed @ThemeColor;
    }
    .top {
      padding-bottom: 18px;
      border-bottom: 1px solid #ddd;
      .icon_upload {
        display: block;
        margin-top: 40px;
        font-size: 52px;
        color: @ThemeColor;
      }
    }
    .p {
      font-size: 16px;
      line-height: 30px;
      margin-top: 10px;
    }
    .tip {
      color: #999;
      font-size: 12px;
      line-height: 18px;
    }
    .btn-upload {
      margin-top: 18px;
      padding: 11px 24px;
    }
    .tip-row {
      margin-top: 20px;
      color: #999;
      font-size: 12px;
    }
  }
  .title {
    font-size: 14px;
    height: 50px;
    line-height: 50px;
  }
  #comTable {
    margin-top: 0;
    .el-table__header thead tr th {
      padding: 7px 5px;
    }
  }
  .footer {
    .upload-tip {
      font-size: 12px;
      color: @ThemeColor;
      margin-left: 20px;
      text-decoration: underline;
    }
  }
}
</style>